@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
* {
    padding: 0;
    margin: 0;
}

.bg-app{
    background-color: #ecf0f3;
}
.btn-shadow{
    box-shadow: 3px 3px 8px #b1b1b1,
                -3px -3px 8px #fff;
    // width: 50px;
    // height: 50px;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    svg{
        transition: all 0.3s ease-in-out;
    }
    &::after{
        content: '';
        transition: opacity 0.3s ease-in-out;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
        opacity: 0;
        border-radius: inherit;
        box-shadow:inset 3px 3px 8px #b1b1b1,
                inset -3px -3px 8px #fff;

    }             
    &:hover{
        box-shadow:none;
        &::after{
            opacity: 1;
        }
        svg{
            fill: #e74c3c;
        }
    }
}
.btn-shadow-darklight{
    // box-shadow: 3px 3px 8px #b1b1b1,
    //             -3px -3px 8px #fff;
    // width: 50px;
    // height: 50px;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease-in-out;
    svg{
        transition: all 0.3s ease-in-out;
    }
    &::after{
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
        opacity: 0;
        border-radius: 50%;
        box-shadow:inset 3px 3px 8px #b1b1b1,
                inset -3px -3px 8px #fff;

    }             
    &:hover{
        box-shadow:none;
        &::after{
            opacity: 1;
        }
        svg{
            &.light{

                fill: #FCD34D;
            }
            &.dark{
                fill: #000;

            }
        }
    }
}
.wrap-avatar{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    height: 130px;
    border: none;
    border-radius: 50%;
    margin: 0 auto;
    box-shadow: 
    inset 3px 3px 3px #a7aaaf,
    /* top left */
    inset -3px -3px 3px #fff;
    .avatar{
        position: relative;
        // margin: 30px;
        width: 100px;
        height: 100px;
        img{
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            object-fit: cover;
            border-radius: 50%;
            box-shadow: 
            3px 3px 3px #a7aaaf,
            -3px -3px 3px #fff
        }
    }
}
.setting{
    position: absolute !important;
}